import React, { Component } from 'react';
import logo from './img/Title.svg';
import playstore from './img/BadgeGoogle.png';
import appstore from './img/BadgeAppStore.png';
import iconFacebook from './img/FacebookBlue.svg';
import iconInstagram from './img/Instagram.svg';
import iconTwitter from './img/Twitter.svg';
import thronescene from './img/CharacterJonDaenerysPhones.svg';
import './type.css'
import './bootstrap.css';
import './App.css';

var Grid = require('react-bootstrap').Grid;
var Row = require('react-bootstrap').Row;
var Col = require('react-bootstrap').Col;

class App extends Component {

  render() {
    return (
      <div className="App">
        <div className="App-header">
        </div>

          <Grid>
            <Row>
              <Col xs={12}><img src={logo} className="App-logo" alt="logo" width="160" height="160"/></Col>
            </Row>
             <Row>
              <Col xs={12}><img src={thronescene} className="App-thronescene" alt="Jon &amp; Danny" /></Col>
            </Row>
             <Row>
              <Col xs={12}><p className="App-description">Draft a team of your favorite GoT Characters, and have them score points across season 7 for glory, fame and fortune. Compete with friends, and score points when your characters defeat their enemies, conquer territory, eat chicken, drink wine, and more!</p></Col>
            </Row>
            <Row>
              <Col xs={12}>
                <div className="App-container">
                <a href="https://www.instagram.com/gameofthronesfantasyapp/"> <img src={appstore} className="App-appstore" alt="GotFantasyApp_IOS" width="135" height="40"/></a>
                <a href="https://play.google.com/store/apps/details?id=com.solen.gotfantasy"> <img src={playstore} className="App-playstore" alt="GotFantasyApp_ANDROID" width="160" height="60"/></a>
                </div>
              </Col>
            </Row>
             <Row>
              <Col xs={12}>
                <div className="App-container App-social">
                  <a href="https://www.facebook.com/gameofthronesfantasy/"><img src={iconFacebook} className="App-social" alt="GotFantasyApp_facebook"/></a>
                  <a href="https://www.instagram.com/gameofthronesfantasyapp/"><img src={iconInstagram} className="App-social" alt="GotFantasyApp_instagram" /></a>
                  <a href="https://twitter.com/GoTFantasyApp/"><img src={iconTwitter} className="App-social" alt="GotFantasyApp_twitter" /></a>
                </div>
              </Col>
             </Row>
           </Grid>
      </div>
    );
  }
}

export default App;



